@import './mixin';

.SearchArea {
  .SearchWrap {
    .BackBtn {
      width: 40px;
      height: 44px;
      span {
        position: relative;
        display: inline-block;
        width: 21px;
        height: 3px;
        background-color: #fff;
        border-radius: 5px;
        &:nth-child(1) {
          transform: rotate(-40deg);
          top: -4px;
        }
        &:nth-child(2) {
          transform: rotate(40deg);
          top: 5px;
        }
      }
    }
    form {
      display: contents;
      input[type="text"] {
        width: 100%;
        height: 44px;
        padding: 0 15px;
        border: none;
        border-radius: 10px;
        font-size: 1.2rem;
      }
      button {
        position: absolute;
        right: 8px;
        top: 9px;
        width: 46px;
        height: 44px;
        background: url('/search_movie/img/total_search.png') no-repeat center;
        background-size: 22px;
      }
    }
    display: flex;
    padding: 9px 8px 8px 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    background-color: #fd3d46;
    box-sizing: border-box;
  }
  .Empty {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 300px;
    font-size: 1.2rem;
  }
  .SearchResult {
    .Movie_Detail {
      width: 100%;
      padding: 15px;
      box-sizing: border-box;
      .Inner {
        position: relative;
        .Movie_Poster {
          position: absolute;
          right: 0;
        }
        .Movie_Title {
          padding: 0 0 15px;
          border-bottom: 1px solid #ececec;
          color: #000;
          span {
            display: block;
            font-weight: normal;
            color: #999;
          }
        }
        .Movie_Info {
          display: flex;
          padding: 15px 0;
          .Movie_Info_List {
            padding-right: 100px;
            box-sizing: border-box;
            li {
              margin-top: 10px;
              word-break: keep-all;
              > span {
                color: #fd3d46;
              }
              dl {
                &::after {
                  content: '';
                  display: block;
                  clear: both;
                }
                dt {
                  float: left;
                  color: #999;
                }
                dd {
                  word-break: initial;
                  span {
                    &::before {
                      content: ', '
                    }
                    &:first-child {
                      &::before {
                        display: none;
                      }
                    }
                  }
                }
              }
              &:first-child{margin-top: 0;}
            }
          }
        }
      }
      .Movie_Stills {
        padding-top: 100px;
        .SubTitle {
          font-size: 1.2rem;
          color: #000;
        }
        .slick-slide {
          img {
            width: 100%;
          }
        }
        .slick-prev {
          background-image: url('/search_movie/img/arrow_left.png');
          &::before {
            display: none;
          }
        }
        .slick-next {
          background-image: url('/search_movie/img/arrow_right.png');
          &::before {
            display: none;
          }
        }
        .slick-prev, .slick-next {
          background-color: rgba(0,0,0,.7);
          background-repeat: no-repeat;
          background-position: center;
          border-radius: 50%;
          z-index: 1;
        }
        .slick-slide {
          padding: 10px;
          box-sizing: border-box;
        }
      }
    }
    .Movie {
      width: 100%;
      padding: 30px 0;
      border-bottom: 1px solid #ececec;
      box-sizing: border-box;
      .Inner {
        position: relative;
        .Movie_Poster {
          position: absolute;
          bottom: 0;
          right: 0;
        }
        .Movie_Title {
          padding: 0 0 15px;
          color: #000;
          span {
            display: block;
            font-weight: normal;
            color: #999;
          }
        }
        .Movie_Info {
          display: flex;
          padding: 15px 0;
          .Movie_Info_List {
            padding-right: 100px;
            box-sizing: border-box;
            li {
              margin-top: 10px;
              word-break: keep-all;
              > span {
                color: #fd3d46;
              }
              dl {
                &::after {
                  content: '';
                  display: block;
                  clear: both;
                }
                dt {
                  float: left;
                  color: #999;
                }
                dd {
                  word-break: initial;
                  span {
                    &::before {
                      content: ', '
                    }
                    &:first-child {
                      &::before {
                        display: none;
                      }
                    }
                  }
                }
              }
              &:first-child{margin-top: 0;}
            }
          }
        }
      }
    }

  }
  
}

